.section {
  padding: 20px;
  margin: 0 auto;
  background-color: greenyellow;
  width: 550px;
}
.titule {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
  color: brown;
}

.list {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  width: 500px;
  border: 1px solid gray;
}

.item {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  padding: 25px;
  width: 125px;
  height: 80px;
  border: 1px solid gray;
}
